<template>
  <div class="about">
    <h1>This is an about page</h1>
    <div>{{value}}</div>
  </div>
</template>

<script>
export default {

  name: 'AboutView',
  data() {
    return {
      value: '',
    }
  },
  mounted() {
    this.$axios.get('https://data.vwer.net/4')
        .then((res) => {
          console.log(res.data)
          this.value = res.data
        }, (error) => {
          console.log('请求数据失败:' + error)
        });
  },
}

</script>
